'use client';

import React, { useState, useEffect } from 'react';
import { Check, Trophy } from 'lucide-react';
import PageLayout from "@/components/layout/PageLayout";
import ModalAlert from "@/components/modal/ModalAlert";
import Image from "next/image";
import { useRouter } from 'next/navigation';
import {
    UserType,
    CheckpointType,
    UserCheckpointType,
    SpinHistoryType,
    SpinHistoryResponseType,
} from '../../types/type'

type CheckpointStatus = 'received' | 'available' | 'locked';
type TabType = 'all' | 'received' | 'redeemed';

export default function Home() {
    const router = useRouter();

    const [rewardName, setRewardName] = useState<string>('');
    const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
    const [activeTab, setActiveTab] = useState<TabType>('all');
    const [userData, setUserData] = useState<UserType | null>(null);
    const [checkpoints, setCheckpoints] = useState<CheckpointType[]>([]);
    const [userCheckpoints, setUserCheckpoints] = useState<UserCheckpointType[]>([]);
    const [spinHistory, setSpinHistory] = useState<SpinHistoryType[]>([]);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);
    const [currentPage, setCurrentPage] = useState<number>(1);

    const userName: string | null = localStorage.getItem("nickname");
    const API_BASE_URL: string = 'http://localhost:8080/api';

    useEffect(() => {
        const fetchData = async (): Promise<void> => {
        try {
            setLoading(true);
            setError(null);

            // 1. Fetch user info
            const userResponse = await fetch(`${API_BASE_URL}/user/${userName}`);
            if (!userResponse.ok) throw new Error('Failed to fetch user data');
            const user: UserType = await userResponse.json();
            setUserData(user);

            // 2. Fetch all checkpoints
            const checkpointsResponse = await fetch(`${API_BASE_URL}/checkpoints`);
            if (!checkpointsResponse.ok) throw new Error('Failed to fetch checkpoints');
            const checkpointsData: CheckpointType[] = await checkpointsResponse.json();
            setCheckpoints(checkpointsData.sort((a, b) => a.pointRequired - b.pointRequired));

            // 3. Fetch user checkpoints
            const userCheckpointsResponse = await fetch(`${API_BASE_URL}/user/checkpoints/${user.id}`);
            if (!userCheckpointsResponse.ok) throw new Error('Failed to fetch user checkpoints');
            const userCheckpointsData: UserCheckpointType[] = await userCheckpointsResponse.json();
            setUserCheckpoints(userCheckpointsData);

            // 4. Fetch spin history based on active tab
            await fetchSpinHistory(user.id, activeTab, currentPage);

            setLoading(false);
        } catch (error) {
            console.error('Error fetching data:', error);
            setError(error instanceof Error ? error.message : 'Unknown error occurred');
            setLoading(false);
        }
        };

        fetchData();
    }, [userName]);

    useEffect(() => {
        if (userData) {
            fetchSpinHistory(userData.id, activeTab, currentPage);
        }
    }, [activeTab, currentPage]);

    const fetchSpinHistory = async (userId: string, tab: TabType, page: number): Promise<void> => {
        try {
            let response: Response;
            if (tab === 'all') {
                response = await fetch(`${API_BASE_URL}/spin-history/${page}`);
            } else {
                response = await fetch(`${API_BASE_URL}/user/spin-history/${userId}/${page}`);
            }
            
            if (!response.ok) throw new Error('Failed to fetch spin history');
            const data: SpinHistoryResponseType = await response.json();
            setSpinHistory(data.spinHistories || []);
        } catch (error) {
            console.error('Error fetching spin history:', error);
        }
    };

    const getCheckpointStatus = (checkpoint: CheckpointType): CheckpointStatus => {
        if (!userData) return 'locked';
        
        const isReceived = userCheckpoints.some(uc => uc.checkpointId === checkpoint.id);
        if (isReceived) return 'received';
        
        if (userData.totalPoint >= checkpoint.pointRequired) return 'available';
        
        return 'locked';
    };

    // const getProgressPercentage = (): number => {
    //     if (!userData || checkpoints.length === 0) return 0;
    //     const maxPoint = Math.max(...checkpoints.map(c => c.pointRequired));
    //     return (userData.totalPoint / maxPoint) * 100;
    // };

    const getProgressPercentage = (): number => {
        if (!userData || checkpoints.length === 0) return 0;

        const lastIndex = checkpoints.length - 1;

        const sumExceptLast = checkpoints
            .slice(0, lastIndex)
            .reduce((sum, c) => sum + c.pointRequired, 0);

        const lastPoint = checkpoints[lastIndex].pointRequired;

        const userPoint = userData.totalPoint;

        if (userPoint <= sumExceptLast) {
            return (userPoint / sumExceptLast) * 45;
        }

        const overLast = userPoint - sumExceptLast;
        return 45 + (overLast / lastPoint) * 55;
    };


    const formatDate = (dateString: string): string => {
        const date = new Date(dateString);
        const day = date.getDate().toString().padStart(2, '0');
        const month = (date.getMonth() + 1).toString().padStart(2, '0');
        const year = date.getFullYear().toString().slice(-2);
        const hours = date.getHours().toString().padStart(2, '0');
        const minutes = date.getMinutes().toString().padStart(2, '0');
        return `${day}/${month}/${year} ${hours}:${minutes} น.`;
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="text-gray-600">กำลังโหลด...</div>
            </div>
        );
    }

    if (error) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="text-center">
                <div className="text-red-600 mb-2">เกิดข้อผิดพลาด</div>
                <div className="text-gray-600 text-sm">{error}</div>
                </div>
            </div>
        );
    }

    return (
        <>
        
            <PageLayout 
                textButton="ไปเล่นเกม" 
                styleButton={{
                    backgroundColor: '#FFC124',
                    color: '#FFFFFF',
                    fontWeight: 'bold',
                    fontSize: '20px',
                    fontFamily: 'Kanit, sans-serif',
                    borderRadius: '100px',
                    height: '48px',
                    width: '100%' 
                }}
                // onClickButton={onClickButton}
            >
                <div className="flex flex-col w-full h-full items-center justify-start bg-white">
                    
                    <div className="w-full bg-[#cccccc] p-4 mb-4">
                        <div className='w-ful bg-white rounded-3xl border border-black border-solid p-4 relative'>
                            <div className="bg-[#B63335] text-white text-sm font-medium px-4 py-2 rounded-r-full inline-block mb-4 absolute left-[0px]">
                                แชร์คะแนน
                            </div>
                            
                            <h2 className="text-right text-xl font-bold mb-2 text-black">สะสมคะแนน</h2>
                            
                            <div className="text-center mb-4">
                                <div className="text-gray-700 mb-1 text-right">
                                    คะแนนครบ {checkpoints[checkpoints.length - 1]?.pointRequired?.toLocaleString()} รับของรางวัล 1 รายการ
                                </div>
                                <div className="text-4xl font-bold text-[#FF2428] text-right">
                                    {userData?.totalPoint?.toLocaleString()}/{checkpoints[checkpoints.length - 1]?.pointRequired?.toLocaleString()}
                                </div>
                            </div>

                            {/* Progress Bar */}
                            <div className="relative mb-6 p-4">
                                <div className="flex justify-between h-7 text-xs text-gray-600 mb-2 relative">
                                    {checkpoints.map((checkpoint, index) => {
                                        const lastIndex = checkpoints.length - 1;
                                        const sumExceptLast = checkpoints
                                        .slice(0, lastIndex)
                                        .reduce((sum, c) => sum + c.pointRequired, 0);

                                        let position: number;

                                        if (index < lastIndex) {
                                            position = (checkpoint.pointRequired / sumExceptLast) * 45;
                                        } else {
                                            position = 100-10;
                                        }
                                        return (
                                            <span 
                                                className='absolute top-0' 
                                                style={{ left: `${position}%`, marginLeft: index === checkpoints.length - 1 ? '-20px' : '-16px', textAlign: index === checkpoints.length - 1 ? 'right' : 'left' }} 
                                                key={checkpoint.id}
                                            >
                                                {checkpoint.rewardName}
                                            </span>
                                        )
                                    })}
                                </div>
                            
                            <div className="relative h-3 bg-gray-200 rounded-full">
                                <div 
                                    className="absolute h-3 bg-gradient-to-r from-orange-400 to-red-500 rounded-full transition-all duration-500"
                                    style={{ width: `${Math.min(getProgressPercentage(), 100)}%` }}
                                />
                                
                                    {/* Checkpoint Markers */}
                                    {checkpoints.map((checkpoint, index) => {
                                        const status = getCheckpointStatus(checkpoint);
                                        const lastIndex = checkpoints.length - 1;
                                        const sumExceptLast = checkpoints
                                        .slice(0, lastIndex)
                                        .reduce((sum, c) => sum + c.pointRequired, 0);

                                        let position: number;

                                        if (index < lastIndex) {
                                            position = (checkpoint.pointRequired / sumExceptLast) * 45;
                                        } else {
                                            position = 100;
                                        }
                                    
                                        return (
                                            <div 
                                                key={checkpoint.id}
                                                className="absolute top-1/2 -translate-y-1/2"
                                                style={{ left: `${position}%`, marginLeft: index === checkpoints.length - 1 ? '-20px' : '-16px' }}
                                            >
                                            <div className={`${
                                                index === checkpoints.length - 1 ? 'w-10 h-10' : 'w-8 h-8'
                                                } rounded-full flex items-center justify-center ${
                                                    status === 'received' 
                                                    ? 'bg-[#096600]'
                                                    : status === 'available'
                                                    ? 'bg-[#cccccc]'
                                                    : ''
                                                }`}>
                                                    {status === 'received' ? (
                                                    <   Check className={`${index === checkpoints.length - 1 ? 'w-6 h-6' : 'w-5 h-5'} text-white`} />
                                                    ) : index === checkpoints.length - 1 ? (
                                                        <Image src="../../img/coin.png" width={30} height={30} alt="icon" />
                                                    ) : null}
                                                </div>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>

                            {/* Action Buttons */}
                            <div className="flex gap-3">
                                {checkpoints.map((checkpoint, index) => {
                                    const status = getCheckpointStatus(checkpoint);
                                    const isReceived = status === 'received';
                                    const isAvailable = status === 'available';
                                    
                                    return (
                                    <button
                                        key={checkpoint.id}
                                        disabled={!isAvailable || isReceived}
                                        className={`flex-1 py-3 rounded-full text-sm font-medium transition-colors ${
                                        isReceived
                                            ? 'bg-pink-400 text-white cursor-not-allowed'
                                            : isAvailable
                                            ? 'bg-red-600 text-white hover:bg-red-700'
                                            : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                                        }`}
                                    >
                                        {isReceived 
                                        ? `รับรางวัล ${index + 1} แล้ว`
                                        : `รับรางวัล ${index + 1}`
                                        }
                                    </button>
                                    );
                                })}
                            </div>
                        </div>
                    </div>

                    {/* Tabs */}
                    <div className="w-full px-6 mb-4">
                        <div className="flex gap-3">
                            <button
                                onClick={() => setActiveTab('all')}
                                className={`px-6 py-2 rounded-full text-sm font-medium border-2 transition-colors ${
                                activeTab === 'all'
                                    ? 'bg-white border-red-500 text-red-500'
                                    : 'bg-white border-gray-300 text-gray-600'
                                }`}
                            >
                                ประวัติทั่วโลก
                            </button>
                            <button
                                onClick={() => setActiveTab('received')}
                                className={`px-6 py-2 rounded-full text-sm font-medium border-2 transition-colors ${
                                activeTab === 'received'
                                    ? 'bg-white border-red-500 text-red-500'
                                    : 'bg-white border-gray-300 text-gray-600'
                                }`}
                            >
                                ประวัติของฉัน
                            </button>
                            <button
                                onClick={() => setActiveTab('redeemed')}
                                className={`px-6 py-2 rounded-full text-sm font-medium border-2 transition-colors ${
                                activeTab === 'redeemed'
                                    ? 'bg-white border-red-500 text-red-500'
                                    : 'bg-white border-gray-300 text-gray-600'
                                }`}
                            >
                                ประวัติรางวัลของฉัน
                            </button>
                        </div>
                    </div>

                    {/* History List */}
                    <div className="w-full px-6 space-y-3">
                        {spinHistory.length === 0 ? (
                            <div className="text-center py-8 text-gray-500">
                                ไม่มีประวัติการหมุน
                            </div>
                        ) : (
                            spinHistory.map((item) => (
                                <div key={item.id} className="bg-white rounded-2xl p-4 shadow-sm flex items-center gap-4">
                                    <div className="w-16 h-16 bg-gradient-to-br from-red-500 to-red-700 rounded-full flex-shrink-0" />
                                        <div className="flex-1">
                                            <h3 className="font-bold text-gray-900 mb-1">
                                                Test {item.id.substring(0, 8)}
                                            </h3>
                                            <p className="text-sm text-gray-500">
                                                รางวัล: {item.rewardPoint.toLocaleString()} | เล่นเมื่อ {formatDate(item.createdAt)}
                                            </p>
                                        </div>
                                </div>
                            ))
                        )}
                    </div>
                </div>
            </PageLayout>

            { isModalOpen && 
                <ModalAlert 
                    icon={<Image src="/public/img/coin.png" width={78} height={78} alt="icon" />}
                    onClose={() => setIsModalOpen(false)}
                    title="ยินดีด้วย"
                    description={"คุณได้รับ" + rewardName}
                    width={300}
                /> 
            }
        </>
    );
}